import { Whop } from "@whop/sdk";

if (!process.env.NEXT_PUBLIC_WHOP_APP_ID) {
  throw new Error("NEXT_PUBLIC_WHOP_APP_ID is not set");
}

if (!process.env.WHOP_API_KEY) {
  throw new Error("WHOP_API_KEY is not set");
}

export const whopsdk = new Whop({
  appID: process.env.NEXT_PUBLIC_WHOP_APP_ID,
  apiKey: process.env.WHOP_API_KEY,
  webhookKey: btoa(process.env.WHOP_WEBHOOK_SECRET || ""),
});

// Platform company ID for receiving payments and sending payouts
export const PLATFORM_COMPANY_ID = process.env.PLATFORM_COMPANY_ID;

/**
 * Transfer funds from platform account to a user (seller payout)
 * Uses Whop's transfers API to move money from the platform's balance
 * to a seller's Whop wallet
 *
 * Note: The actual Whop SDK types may differ. This function uses any types
 * and should be updated when the actual API structure is confirmed.
 */
export async function createPayoutTransfer(params: {
  recipientUserId: string;
  amountCents: number;
  description: string;
  idempotencyKey: string;
  metadata?: Record<string, string>;
}): Promise<{ transferId: string; status: string }> {
  if (!PLATFORM_COMPANY_ID) {
    throw new Error("PLATFORM_COMPANY_ID is not set");
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const response = await (whopsdk.transfers as any).create({
    origin: PLATFORM_COMPANY_ID,
    destination: params.recipientUserId,
    amount_cents: params.amountCents,
    currency: "usd",
    description: params.description,
    idempotency_key: params.idempotencyKey,
    metadata: params.metadata,
  });

  return {
    transferId: response.id,
    status: response.status || "pending",
  };
}

/**
 * Check the status of a transfer
 */
export async function getTransferStatus(
  transferId: string
): Promise<{ status: string; failureReason?: string }> {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const response = await (whopsdk.transfers as any).retrieve(transferId);

  return {
    status: response.status || "unknown",
    failureReason:
      response.status === "failed" ? response.failure_message : undefined,
  };
}

/**
 * Create a checkout configuration for purchasing hours
 * Uses Whop's checkoutConfigurations API which creates a plan and returns
 * a checkout config that can be used with iframeSdk.inAppPurchase()
 */
export async function createCheckoutConfig(params: {
  name: string;
  amountCents: number;
  currency: "usd";
  metadata: Record<string, string>;
  redirectUrl?: string;
}): Promise<{ id: string; planId: string; purchaseUrl: string }> {
  if (!PLATFORM_COMPANY_ID) {
    throw new Error("PLATFORM_COMPANY_ID is not set");
  }

  // Create checkout configuration with an inline plan
  // Note: Whop's initial_price expects dollars, not cents
  const response = await whopsdk.checkoutConfigurations.create({
    plan: {
      company_id: PLATFORM_COMPANY_ID,
      currency: params.currency,
      initial_price: params.amountCents / 100,
      plan_type: "one_time",
      title: params.name,
      visibility: "hidden",
      product: {
        external_identifier: `waliet_hours_${Date.now()}`,
        title: params.name,
        business_type: "coaching",
      },
    },
    metadata: params.metadata,
    redirect_url: params.redirectUrl,
  });

  if (!response || !response.plan) {
    throw new Error("Failed to create checkout configuration");
  }

  return {
    id: response.id,
    planId: response.plan.id,
    purchaseUrl: response.purchase_url,
  };
}
